<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li>후기</li>
			<li class="current">문의</li>
		</ul>

		<div class="contents-container">
			<div class="search-box">
				<div class="select-box">
					<SelectBoxAdmin
						class="small marR-10"
						:options="categories"
						v-model="categorySelected"
						placeholder="카테고리를 선택해주세요"
					/>
				</div>
				<div class="select-box">
					<SelectBoxAdmin
						class="small marR-10"
						:options="types"
						v-model="typeSelected"
						placeholder="작성자를 선택해주세요"
					/>
				</div>
				<input
					type="text"
					class="admin-input small marR-10"
					placeholder="검색어를 입력하세요"
					v-model="keyword"
				/>
				<button class="outlined-btn" type="button" @click="searchAdminQuestion">
					검색
				</button>
			</div>
			<ul class="table board">
				<li class="row props">
					<div class="prop check">
						<input
							type="checkbox"
							class="checkbox"
							name="member"
							id="select-all"
							v-model="selectAll"
						/>
						<label for="select-all"></label>
					</div>
					<div class="prop">카테고리</div>
					<div class="prop large">제목</div>
					<div class="prop">작성자</div>
					<div class="prop">작성일자</div>
				</li>
				<slot v-if="!questionList">
					<li class="row no-data body3" style="display: block;">
						등록된 글이 없습니다
					</li>
				</slot>
				<slot v-else>
					<ul v-for="(item, indx) in questionList" :key="indx">
						<li class="row">
							<div class="prop check">
								<input
									type="checkbox"
									class="checkbox"
									name="member"
									:id="item.id"
									:value="item.id"
									v-model="selected"
								/>
								<label :for="item.id"></label>
							</div>
							<div class="prop">
								{{ showEnumAsString(item.categoryId) }}
							</div>
							<router-link
								:to="{ name: 'AdminQuestionDetail', params: { id: item.id } }"
								:key="item.id"
								class="marR-20"
							>
								<div class="prop large txt-left">
									{{ item.title }}
								</div>
							</router-link>
							<div class="prop">{{ item.createId }}</div>
							<div class="prop ">
								{{ $moment(item.updateDate).format('YYYY.MM.DD') }}
							</div>
						</li>
					</ul>
				</slot>
			</ul>
			<div class="bottom-container">
				<Pagination
					v-if="totalPage !== -1"
					:listRowCt="15"
					:pageLinkCt="5"
					:totalCount="totalPage"
					:url="`/admin/question`"
				/>
				<div class="btn-container">
					<div class="checked-info body3 marR-30">
						<span class="number">{{ selectedCnt }}</span
						><span>개 선택됨</span>
					</div>

					<button
						class="solid-btn red"
						@click="isDeleteModalViewed = true"
						:disabled="selected.length < 1"
					>
						선택 글 삭제
					</button>
				</div>
			</div>
			<ModalView
				v-if="isDeleteModalViewed"
				@close-modal="isDeleteModalViewed = false"
			>
				<DeleteItem
					@close-modal="isDeleteModalViewed = false"
					@confirm="deleteAdminQuestion"
					:count="selected.length"
				></DeleteItem>
			</ModalView>
		</div>
	</div>
</template>
<script>
import {
	fetchAdminQuestions,
	deleteAdminQuestion,
	fetchQuestionsTotalCount,
	searchAdminQuestion,
} from '@/api/question';
import Pagination from '@/components/common/Pagination';
import DeleteItem from '@/views/admin/notice-news/components/DeleteItem';
import ModalView from '@/components/common/ModalView';
import SelectBoxAdmin from '@/components/common/SelectBoxV2';
import { enumToString } from '@/utils/enumToString';

export default {
	components: { DeleteItem, ModalView, Pagination, SelectBoxAdmin },
	created() {
		this.currentPage = this.$route.params.page;
		this.fetchAdminQuestions(this.currentPage);
		this.fetchQuestionsTotalCount();
	},

	data() {
		return {
			categories: [
				{ id: 'AC', label: '계정' },
				{ id: 'PY', label: '결제' },
				{ id: 'SH', label: '배송' },
				{ id: 'GD', label: '굿즈' },
				{ id: 'ER', label: '교환 및 환불' },
				{ id: 'ETC', label: '기타' },
			],
			types: [
				{ id: 'T', label: '제목' },
				{ id: 'I', label: '아이디' },
			],
			categorySelected: {},
			typeSelected: {},
			questionList: [],
			categoryList: [],
			selected: [],
			totalPage: -1,
			currentPage: 1,
			keyword: '',
			isLoading: false,
			isDeleteModalViewed: false,
		};
	},
	computed: {
		selectedCnt() {
			return this.selected.length;
		},
		selectAll: {
			get: function() {
				return this.questionList
					? this.selected.length == this.questionList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.questionList.forEach(function(item) {
						console.log(item.id);
						selected.push(item.id);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchAdminQuestions(page) {
			this.isLoading = true;
			try {
				const { data } = await fetchAdminQuestions(page - 1);
				console.log(data);
				this.questionList = data.questionList;
				this.categoryList = data.categoryList;
			} catch (e) {
				console.log(e);
				alert(e);
			}
		},
		showEnumAsString(enumId) {
			console.log('enumId', enumId);
			return enumToString(enumId);
		},
		async searchAdminQuestion() {
			try {
				const { data } = await searchAdminQuestion(
					this.currentPage - 1,
					this.categorySelected.id,
					this.keyword,
					this.typeSelected.id,
				);
				this.questionList = data;
			} catch (e) {
				alert('에러가 발생했습니다 ');
			}
		},
		async deleteAdminQuestion() {
			this.isLoading = true;
			try {
				await deleteAdminQuestion(this.selected);
				await this.fetchAdminQuestions(this.currentPage);
				this.selected = [];
				this.isDeleteModalViewed = false;
			} catch (e) {
				alert(e);
			}
		},
		async fetchQuestionsTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchQuestionsTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.fetchAdminQuestions(this.currentPage);
			}
		},
	},
};
</script>
<style scoped>
.search-box {
	display: flex;
}
.small {
	width: 200px;
}
.middle {
	width: 200px;
}
.large {
	width: 625px;
}
</style>
